const urlDict = {
  userSearch: '/user/search',
  userRegister: '/user/register',
  userUpdate: '/user/update',
  userRead: '/user/read',
  vendorSearch: '/vendor/search',
  userEnable: '/user/enable',
  user_searchGroupList: '/user/searchGroupList',
}
var api = {
  userSearch: function (value) {
    return this.HttpRequest.POST(urlDict.userSearch, value)
  },
  userRegister: function (value) {
    return this.HttpRequest.POST(urlDict.userRegister, value)
  },
  userUpdate: function (value) {
    return this.HttpRequest.POST(urlDict.userUpdate, value)
  },
  userRead: function (value) {
    return this.HttpRequest.POST(urlDict.userRead, value)
  },
  vendorSearch: function (value) {
    return this.HttpRequest.POST(urlDict.vendorSearch, value)
  },
  userEnable: function (value) {
    return this.HttpRequest.POST(urlDict.userEnable, value)
  },
  user_searchGroupList: function (value) {
    return this.HttpRequest.POST(urlDict.user_searchGroupList, value)
  }
}
module.exports = api;



