require("./addUser.less");
class addUser extends Interstellar.modalBase {

    constructor(app, dom, value, addMode) {
        super(app, dom, value, addMode)
        this.html = require('./tpl.html')
        this.name='addUser';
        this.apidata = {
            hospital:'杏脉医学'
        }
    }

    //var endData = []
    complete() {
        let that=this;

        this.initView()
        console.log(that.api.type,'that.api.type')

        if(that.api.title){
            that.dom.find('.modal-title').html(that.api.title)
        }
        require.ensure("../../moduleslibs/dropdown1/drop.js", function() {
            let dropdown = require("../../moduleslibs/dropdown1/drop.js")
            let status = that.app.loadModule(dropdown, that.dom.find('.role'), {
                className:'xlk',
                firstSelect: { val: '', idx: '' },
                data: [{ val: '标注医生', idx: 'doctor.tourist' },{ val: '科研医生', idx: 'doctor.research' },{ val: '审核医生', idx: 'doctor.audit' }]
            })
            status.event._addEvent('option.click', function (value) {
              // if(value.idx=='doctor.audit'){
              //   that.apidata.auditQualification = true
              //   that.apidata.roleListCode='doctor.tourist'
              // }else{
              //   that.apidata.auditQualification = false
                that.apidata.roleListCode=value.idx
              // }
            })
          if(that.api.type=='edit0'){
            that.dom.find('.modal-title').html('编辑内部用户')
            that.setData(that.api.data)
            that.apidata.name=that.api.data.name
            that.apidata.mobilePhone=that.api.data.mobilePhone
            that.dom.find('.checkboxarea').remove()
          }else if(that.api.type=='edit1'){
            that.dom.find('.modal-title').html('编辑外部用户')
            that.setData(that.api.data)
            that.apidata.name=that.api.data.name
            that.apidata.mobilePhone=that.api.data.mobilePhone
          }else if(that.api.type=='newinner'){
            that.dom.find('.checkboxarea').remove()
            that.dom.find('.role').parent().remove()
          }
        })
    }
    setData(value){
        let that=this;
        that.dom.find('.inputBox[api=name]').val(value.name)
        that.dom.find('.inputBox[api=mobile]').val(value.mobilePhone)
        that.dom.find('.inputBox[api=mobile]').attr('disabled','disabled')
        if(value.roleCodeList[0]!=='ADMIN'){
          that.dom.find('.role .option[data-idx="'+value.roleCodeList[0]+'"').click()
        }else{
          that.dom.find('.role').parent().remove()
        }
        // if(value.auditQualification){
        //   that.dom.find('.role .option[data-idx="doctor.audit"]').click()
        // }
    }
   initView () {
        let that=this;
        that.dom.find('.inputBox').on('change',function () {
            that.apidata[ES.selctorDoc(this).attr('api')]=ES.selctorDoc(this).val()
        })
       this.dom.find('.icon-guanbi').on('click', function() {
           that.close()
       })
       this.dom.find('.btn-cancel').on('click', function() {
           that.close()
       })
       // this.dom.find('.checkboxarea i').on('click', function() {
       //     let dom=ES.selctorDoc(this)
       //     if(dom.hasClass('choose')){
       //         dom.removeClass('choose')
       //         that.apidata.auditQualification=false
       //     }else{
       //         dom.addClass('choose')
       //         that.apidata.auditQualification=true
       //     }
       // })
        this.dom.find('.btn-confirm').on('click', function() {
            that.dom.find('.inputLine').dom.forEach(function(val, idx) {
                val.find('.'+val.attr('redlabel')).removeClass('redborder')
                val.find('.required').remove();
                if(Tool.checkForm(ES.selctorDoc(val).dom,'red')!==''){
                    val.find('.'+val.attr('redlabel')).addClass('redborder')
                    val.find('.'+val.attr('redlabel')).after('<span class="required">'+Tool.checkForm(ES.selctorDoc(val).dom,'red')+'</span>')
                }
            })
            if(that.dom.find('.redborder').dom){
                return false
            }else{
                that.event._dispatch('addUser.submit',that.apidata)
            }
            // that.dom.find('.required').remove()
            // if(that.dom.find('.name').val()==''){
            //     that.dom.find('.inputBox').after('<span class="required">必填</span>')
            // }else if(that.dom.find('.name').val()==''){
            //     that.dom.find('.inputBox').after('<span class="required">必填</span>')
            // }else{
            //     that.event._dispatch('addUser.submit',that.apidata)
            // }
        })
    }

}
//原型链一定要有的
module.exports = addUser;



