class accountmanage extends DataBase {
  complete() {
    let that = this;
    this.changeAll(this.model.condition)
    this.model.tableconfig.tablewidth = ES.selctorDoc('.accountmanage').box().clientWidth - 140
    this.loadlist('group')
    this.addUser1 = require("../modal/addUser1/addUser")
    this.apidata={}
    //新增用户
    this.dom.find('.create').on('click', async () => {
      this.adduser = this.app.loadModal(this.addUser1, {adv: true}, {type: 'newinner'})
      this.adduser.event._addEvent('addUser.submit', async (value) => {
        value.roleCodeList = ['ADMIN']
        value.password = 'Fosun@123'
        this.app.loading.show()
        let res = await this.api.userRegister(value)
        this.app.loading.hide()
        if (res.code == 0) {
          this.adduser.close()
          this.apidata.page = 1;
          this.search(true)
        } else{
          this.adduser.dom.find('.inputBox').after('<span class="required">'+res.msg+'</span>')
        }
      })
    })
    this.dom.find('.search').on('click', () => {
      this.model.condition.forEach((val) => {
        this.model.apiData[val[0].name] = this.getfiltercondition()[val[0].name].toString() == '' ? null : this.getfiltercondition()[val[0].name].toString()
      })
      if (that.model.apiData.codes) {
        let temp = []
        temp.push(that.model.apiData.codes)
        that.model.apiData.codes = temp
      }
      this.model.apiData.page = 1;
      this.search(true)
    })
    this.dom.find('.filterarea input').on('change', function(){
      that.apidata[ES.selctorDoc(this).attr('api')] = ES.selctorDoc(this).val();
    })
  }

  getapidata(value) {
    console.log(value,'hxx')
    if (value) {
      switch (value.name) {
        case 'vendorName':
          this.chooseData.moduleobj.vendorName.loading(true)
          this.vendorSearch(value.data.data ? value.data.data.trim() : "");
          break;
      }
    } else {
      this.chooseData.moduleobj.vendorName.loading(true)
      this.vendorSearch('');
      this.user_searchGroupList('');
    }
  }
  async vendorSearch(name) {
    let {data} = await this.api.vendorSearch({name, page: 1, pageSize: 10})
    this.chooseData.moduleobj.vendorName.loading(false)
    let temparr = data.list.map((item) => {
      return {
        idx:item.name,
        val:item.name
      }
    })
    this.chooseData.renderHtml(temparr, 'vendorName')
  }

  async user_searchGroupList() {
    let {data} = await this.api.user_searchGroupList({})
    this.chooseData.moduleobj.codes.loading(false)
    this.rolelist = data.map((item) => {
      return {
        idx:item.code,
        val:item.name
      }
    })
    this.chooseData.renderHtml(this.rolelist, 'codes')
  }

  async listaction(value) {
    switch (value.classname) {
      case 'bianji':
        this.app.loading.show()
        let json = {
          id: value.id.split(',')[0]
        }
        let res = await this.api.userRead(json)
        this.app.loading.hide()
        if (res.code == 0) {
          this.adduser = this.app.loadModal(this.addUser1, {adv: true}, {
            type: 'edit' + value.id.split(',')[1],
            data: res.data
          })
          this.adduser.event._addEvent('addUser.submit',async (data) => {
            data.id = value.id.split(',')[0]
            if(data.roleListCode){
              data.roleCodeList=[data.roleListCode];
            }
            this.app.loading.show()
            let res = await this.api.userUpdate(data)
              this.app.loading.hide()
              if (res.code == 0) {
                this.adduser.close();
                this.apidata.page = 1;
                this.search(true)
              } else{
                this.adduser.dom.find('.inputBox').after('<span class="required">'+res.msg+'</span>')
              }
          })
        }
        break;
    }
  }

  async listOnOff(value){
    this.app.alert.show({
      title: ' ',
      msg: '确定切换该用户状态？',
      close: true,
      sure: async () => {
        let json = {
          id: value.id.split(',')[0],
          enable: value.action == 'on' ? true : false
        }
        let res = await this.api.userEnable(json)
        if (res.code == 0) {
          this.search()
        } else {
          alert('失败')
          this.search()
        }
      },
      closeSure: () => {
        this.search()
      }
    })
  }

  //查询全部成员/管理员小组成员
  async search(bool) {
    let data2 = []
    this.tablecont.showloading()
    let res= await this.api.userSearch(this.model.apiData)
    if (res.code == 0) {
      if (res.data.list.length > 0) {
        res.data.list.forEach((val) =>{
          for (let i in val) {
            val[i] = val[i] == null ? '' : val[i]
          }
          let obj = {}
          val.auditstatus = ''
          obj.id = val.id + ',0';
          switch (val.status) {
            case 'Y':
              val.status = true;
              obj.operation = this.model.listicon.action;
              break;
            case 'N':
              val.status = false;
              obj.operation = this.model.listicon.action;
              break;
          }
          data2.push(obj)
          let tempStr = ''
          val.roleCodeList.forEach((v, i) => {
            this.rolelist.forEach((item)=>{
              if(v == item.idx){
                if(item.val.lastIndexOf('管理员')==-1){
                  obj.id = val.id + ',1'
                  if(val.auditQualification){
                    tempStr += '审核医生'
                  }else{
                    tempStr += item.val
                  }
                }else{
                  tempStr += item.val
                }
              }
            })
            // if (v == 'ADMIN') {
            //   tempStr += '内部管理员'
            // } else {
            //   tempStr += '外部医生'
            //   tempStr += val.auditQualification?'(审核)':''
            //   obj.id = val.id + ',1'
            // }
          })
          val.roleCodeList = tempStr
        })
        this.tablecont.setData(res.data.list, data2)
      } else {
        this.tablecont.noData();
      }
    }
    if (bool) {
      this.tablecont.getTotal(res.data.pages, 2, res.data.total)
    }
    this.initscroll();
  }

  initscroll() {
    if (this.myScroll) {
      this.myScroll.refresh()
      return
    }
    var rid = 'aaa_' + Math.floor(new Date().getTime() * Math.random())
    ES.selctorDoc('.scrolltable').attr('id', rid)
    this.myScroll = new IScroll('#' + rid, {
      scrollbars: true,
      mouseWheel: true,
      scrollX: true,
      interactiveScrollbars: true,
      hideScrollbar: false,
      vScrollbar: true,
      shrinkScrollbars: 'scale',
      fadeScrollbars: false,
      disableMouse: true,
      disablePointer: true
    });
  }

  resize() {
    let cw = ES.selctorDoc(window).box().clientWidth - 240
    let ch = ES.selctorDoc(window).box().clientHeight - 100
    ES.selctorDoc('.accountmanage').css({'height': ch, 'width': cw})
    ES.selctorDoc('.accountlist').css({'width': cw});
    ES.selctorDoc('.scrolltable').css({'height': ch - 200});
  }
}

module.exports = accountmanage;



