//这边基本上引入需要使用的资源less，api，需要使用的模块等等。
class accountmanage extends Interstellar.modelBase {
  constructor(app) {
    super(app)
    this.apiData = {page: 1, pageSize: 10}
    this.condition =[
      [{ "type": "texts", "name": "name", "showname": "用户姓名", "datatype": "obj", "key": "name", "out": true }],
      [{ "type": "texts", "name": "mobile", "showname": "手机号", "datatype": "obj", "key": "mobile", "out": true }],
      [{ "type": "dropdownS", "name": "vendorName", "showname": "供应商", "datatype": "obj", "data": [], "key": "vendorName", "out": true,"input":true }],
      [{ "type": "dropdownS", "name": "codes", "showname": "用户身份", "datatype": "obj", "data": [], "key": "codes", "out": true}],
    ]
    this.tableconfig = {
      icon: {
        "name": {name: '<span data-i18n="pname">用户姓名</span>', type: 'text', w: '18%', ww: '18%', n: '40'},
        "idNumber": {name: '<span data-i18n="zj">身份证号</span>', type: 'text', w: '17%', ww: '17%'},
        "mobilePhone": {name: '<span data-i18n="gh">手机号</span>', type: 'text', w: '17%', ww: '17%'},
        "vendorName": {name: '<span data-i18n="yymc">所属标注供应商</span>', type: 'text', w: '18%', ww: '18%'},
        "roleCodeList": {name: '<span data-i18n="gh">用户身份</span>', type: 'text', w: '17%', ww: '17%'},
        "status": {name: '<span data-i18n="gh">用户状态</span>', type: 'onoff', w: '13%', ww: '13%', onofftype: 'enable'}
      },
      onofftype:{0: '无效', 1: '有效'},
      type: 'center',
      actionicon: {
        "operation": { name: '<span data-i18n="action" data-name="操作">操作</span>', type: 'action', code: 'action', w: '100%', ww: '100%' }
      }
    }
    this.listicon = {
      action: {//待发布
        bianji: {dis: 'inline', link: 'noLink', titleText: '编辑'}
      }
    }
  }
}

module.exports = accountmanage;



